import React from 'react';
import DesktopOrLaptop from '../../../shared/devices/DesktopOrLaptop';
import TabletOrMobile from '../../../shared/devices/TabletOrMobile';
import Carousel from '../../shared/Carousel';
import { Wrapper, StyledSpan, StyledDiv, Bold } from './index.style';

const ContentSection = () => {

    const images = [
        {
            image: `${process.env.PUBLIC_URL}\\images\\SosPrecesImage3.png`,
            caption: ''
        }
    ];

    return (
        <>
            <DesktopOrLaptop>
                <Wrapper desktop>
                    <StyledSpan>
                        <p> Esta atividade tem por objetivo prestar assistncia espiritual e
                            emocional s pessoas que ligam para o telefone <Bold>(31) 3334-9700</Bold>,
                            em busca de alvio, conforto e fora para
                            enfrentar as vicissitudes da vida. </p>
                        <p>  um atendimento gratuito pelo qual o atendido est, normalmente, sofrendo por
                            problemas emocionais, familiares, de sade e diversos outros semelhantes,
                            que trazem desnimo, depresses e, muitas vezes, situaes de
                            tentativas de suicdios.  </p>
                        <p>  um trabalho que muito gratifica nossa Casa. </p>
                    </StyledSpan>
                    <StyledDiv>
                        <Carousel data={images} />
                    </StyledDiv>
                </Wrapper>
            </DesktopOrLaptop>
            <TabletOrMobile>
                <Wrapper>
                    <StyledSpan>
                        <p> Esta atividade tem por objetivo prestar assistncia espiritual e
                            emocional s pessoas que ligam para o telefone <Bold>(31) 3334-9700</Bold>,
                            em busca de alvio, conforto e fora para
                            enfrentar as vicissitudes da vida. </p>
                        <p>  um atendimento gratuito pelo qual o atendido est, normalmente, sofrendo por
                            problemas emocionais, familiares, de sade e diversos outros semelhantes,
                            que trazem desnimo, depresses e, muitas vezes, situaes de
                            tentativas de suicdios.  </p>
                        <p>  um trabalho que muito gratifica nossa Casa. </p>
                    </StyledSpan>
                    <StyledDiv>
                        <Carousel data={images} />
                    </StyledDiv>
                </Wrapper>
            </TabletOrMobile>
        </>
    );
};

export default ContentSection;