/* eslint-disable max-lines */
import React from 'react';
import { Wrapper, StyledSpanText, StyledSpanTitle, StyledDiv, StyledDivContent, Bold, Italic } from './index.style';
import Accordion from '../../shared/Accordion';
import Carousel from '../../shared/Carousel';
import DesktopOrLaptop from '../../../shared/devices/DesktopOrLaptop';
import TabletOrMobile from '../../../shared/devices/TabletOrMobile';

const ContentSection = () => {

    const questionsAndAnswers = [
        {
            key: 1,
            title: 'O que os membros tm em comum?',
            content: (
                <>
                    <p> Os membros do grupo tm em comum o fato de estarem buscando se libertar
                    da dependncia. Uns lutam para vencer as drogas ou os comportamentos
                    compulsivos (jogo, comida, sexo, limpeza, etc), outros lutam contra
                    os vcios morais. </p>
                    <p> A luta pela libertao , portanto, o trao comum entre os membros do
                    grupo. Para isso, todos esto dispostos a adotarem novos hbitos e um
                    novo estilo de vida. </p>
                </>
            )
        },
        {
            key: 2,
            title: 'Que tipo de pessoas so admitidas no grupo?',
            content: (
                <>
                    <p> Podem participar do grupo pessoas de ambos os sexos, de qualquer idade,
                      de qualquer raa, condio social, crena religiosa ou poltica, etc. </p>
                    <p> No h restries quanto ao aspecto fsico ou condio de sade do
                      participante, porm todos devem se enquadrar nos critrios de
                      admisso. </p>
                </>
            )
        },
        {
            key: 3,
            title: 'Quais so os critrios de admisso?',
            content: (
                <>
                    <p> Para serem admitidos os candidatos devem: </p>
                    <p> 1. Manifestar interesse de se curar. </p>
                    <p> 2. Participar do programa de livre e espontnea vontade. </p>
                    <p> 3. Aceitar os termos dos Compromissos dos membros do grupo </p>
                </>
            )
        },
        {
            key: 4,
            title: 'Quais os fundamentos do Programa?',
            content: (
                <>
                    <p> O programa se fundamenta nas seguintes constataes: </p>
                    <p> 1.As dependncias tm razes no Esprito. O Esprito  a sede
                      dos desejos, das emoes, das concepes e valores do ser. Primeiro,
                      a dependncia espintual, depois a dependncia fisica. Assim,
                      a prioridade do programa, o seu objtivo principal,  o Esprito,
                      essncia do ser. </p>
                    <p> 2. Se a raz da dependncia est no esprito, a cura s pode vir
                      de dentro para fora. Assim, a rigor somente existe autocura e ns
                      somos agentes (e no pacientes) da nossa prpria cura. </p>
                    <p> 3. O remdio seguro para a nossa autocura  a autoeducao, atravs
                      da qual modificamos os nossos valores e conceitos, nossa forma de
                      enxergar a vida e o nosso papel dentro dela. </p>
                    <p> 4. Atravs do pensamento voltado para os vcios, atramos companhias
                      espirituais afins, que acabam por interferir em nossa maneira de agir. </p>
                    <p> 5. Incentivos ao hbito da orao com o duplo objetivo: restabelecer o
                      contato com Deus e com as foras Espirituais superiores; Evitar a
                      sintonia inferior. </p>
                    <p> 6. Tratamentos espirituais diretos com passes e gua fluidificada. </p>
                    <p> 7. Acompanhamento do progresso individual, atravs do qual cada um 
                      auxiliado a superar suas dificuldades. </p>
                </>
            )
        },
        {
            key: 5,
            title: 'Sendo um programa esprita, o participante precisa se tornar esprita para usufruir dos seus benefcios?',
            content: (
                <>
                    <p> No. A ningum ser exigido tornar-se esprita. Entendemos que todas
                      as religies cumprem um papel determinado na evoluo individual de
                      cada ser e, assim, no de toda a humanidade. Cada pessoa procura
                      estabelecer contato com Deus e desenvolver o seu lado espiritual
                      atravs da doutrina que melhor satisfaa as necessidades mais urgentes
                      do seu processo evolutivo. Mas todos os caminhos de bons princpios
                      levam a Deus. </p>
                    <p> Para usufruir dos benefcios do programa, basta que o participante
                      queira mudar, que esteja aberto  crena num poder superior e se esforce
                      para viver dentro dos princpios morais cristos que, em ltima anlise,
                      se encontram na base de todas as religies e doutrina srias. O programa
                      oferece subsdios para que aprendamos a viver dentro desses princpios.
                      No mais, a ajuda vem de Deus. </p>
                </>
            )
        },
        {
            key: 6,
            title: 'Foi dito que a prioridade do programa  o Esprito. Como  que se faz o tratamento do Esprito?',
            content: (
                <>
                    <p> A recuperao do Esprito ocorre atravs da reforma ntima. Reformar-se
                      intimamente significa superar as fraquezas e deficincias morais, que
                      os levam aos vcios, substituindo-as por sentimentos, emoes e
                      comportamentos enobrecidos e espiritualmente elevados. </p>
                    <p> A reforma ntima, portanto, vem do autoconhecimento e da autoeducao.
                      Ela  resultado de um movimento interno, conscientemente direcionado
                      para a mudana de hbitos, de pensamentos, de desejos e de sentimentos.
                      Logo, o programa no &quot;trata o esprito&quot;, pois somente ele prprio pode
                      se tratar, provocando em si mesmo esse &quot;movimento&quot;, esta ao para a
                      mudana. </p>
                    <p> O que o programa esprita faz  oferecer subsdios para que o
                      participante atinja esse estgio de concincia da necessidade da
                      mudana. Dessa conscincia real que transforma o desejo da mudana
                      em ao concreta para realiz-la. </p>
                </>
            )
        },
        {
            key: 7,
            title: 'Mas enquanto o participante no atinge esse estgio de busca da reforma ntima, ' +
             'como o Programa o ajuda a se manter afastado dos vcios?',
            content: (
                <>
                    <p> Enquanto buscamos incorporar novas atitudes, novos hbitos em nossas
                      vidas, o programa nos oferece ferramentas, instrumentos e tcnicas
                      que nos auxiliam a manter a abstinncia. Muitas so dinmicas de
                      grupo, que nos auxiliam nesses objetivos. </p>
                    <p> No podemos nos esquecer, no entanto, que um tratamento espiritual
                      est sendo feito paralelamente, com o objetivo de nos aliviar do assdio
                      espiritual e ainda de desintoxicar o nosso corpo perispiritual. Alm
                      disso, contamos com os passes magnticos e com a gua fluidificada. </p>

                </>
            )
        },
        {
            key: 8,
            title: 'No vou ficar entediado(a) com as reunies?',
            content: (
                <>
                    <p> Sabemos que a maioria das pessoa no gosta de reunies. A prpria
                      palavra &quot;reunio&quot; costuma ser associada a obrigao, o que, para
                      ns que sempre buscamos o prazer, soa como tdio ou monotonia. </p>
                    <p> Justamente para evitarmos que nossas reunies se tornem montonas
                      e repetitivas, buscamos variar constantemente a sua temtica e,
                      por isso, fazemos diferentes tipos de reunies. Assim, nunca
                      ficamos entediados e, toda semana, ficamos aguardando ansiosamente
                      o dia em que nos reuniremos de novo. </p>

                </>
            )
        },
        {
            key: 9,
            title: 'Algum vai me pressionar a falar da minha dependncia?',
            content: (
                <>
                    <p> No nosso programa no falamos dos vcios e nem das drogas, falamos
                      da cura, do nosso progresso individual e coisas assim. Para ns, no
                      interessa em que tipo de drogas ou comportamentos nos viciamos.
                      Interessa-nos somente a cura, a libertao. </p>
                    <p> H um dia especial para depoimentos. Nesse dia, fala quem quer sobre
                      o que quiser. Mas o objetivo  o autoconhecimento, no uma espcie
                      de &quot;confisso de culpa&quot;. </p>
                    <p> Ao falar, o participante tem contato com os seus medos, as suas dvidas,
                      vitrias e fracassos. Cada um fala o que quiser. Pode relatar um
                      problema em casa, o progresso na relao familiar, uma recada ocorrida
                      na semana, um fato em que se saiu vitorioso, etc. </p>
                </>
            )
        },
        {
            key: 10,
            title: 'Como nos comportarmos nas reunies de depoimentos?',
            content: (
                <>
                    <p> Antes dos trabalhos, o coordenador instrui o grupo sobre como se
                      comportar durante os depoimentos, colocando, basicamente, o que se
                      segue: </p>
                    <p> 1. Nenhum participante do grupo deve criar polmica, pretender
                      debater ou criticar o depoimento de um companheiro. No importa o
                      que pense sobre o que ouvir, dever se manter calado e em atitude
                      de respeito. </p>
                    <p> 2. Ningum dever fazer perguntas ou incitar algum membro do grupo
                      a falar. </p>
                    <p> 3. Ningum dever desmentir o companheiro ou revelar coisas que ele
                      prprio no queira revelar. </p>
                    <p> 4. Nenhum membro do grupo citar o nome de outro membro em seu
                      depoimento. </p>
                    <p> 5. Uma mxima do grupo  sempre lembrada: &quot;O que voc v aqui...
                      o que voc ouve aqui... voc deixa aqui&quot;. </p>
                    <p> 6. Quem no se sentir  vontade para falar, no ser forado.
                      Falar ou no  uma deciso de cada um e dever ser respeitada
                      pelo grupo. </p>
                </>
            )
        },
        {
            key: 11,
            title: 'Teremos de ir a estas reunies para o resto da nossa vida?',
            content: (
                <>
                    <p> No, a no ser que se queira. A maioria de ns parece gostar cada
                      vez mais das reunies  medida que passam os anos de sobriedade.
                      Deste modo, as reunies so um prazer e no uma obrigao. </p>
                    <p> Alguns de ns faz da frequncia das reunies um hbito, como trabalhar,
                      ler, assistir a espetculos desportivos, a frequentar clubes sociais e
                      participar de cultos religiosos. Isto torna mais slida a base da nossa
                      recuperao. </p>
                    <p> Porm, a maior parte de ns vai s reunies com mais frequncia nos
                      primeiros anos de recuperao. Consideramos isso muito importante,
                      porque no princpio, quando estamos tentando manter a abstinncia,
                      ficamos mais fortalecidos. </p>
                    <p> Mas consideramos um erro deixar de participar do grupo ou espaar
                      a frequncia quando estamos mais fortalecidos, porque podemos ficar
                      muito autoconfiantes e recarmos. Por causa disso, o nosso programa
                      pede a todos os participantes que no se ausentem do grupo antes de
                      completarem, no mnimo, dois anos de frequncia. </p>
                </>
            )
        }
    ];

    const images = [
        {
            image: `${process.env.PUBLIC_URL}\\images\\ProgramaRenascerLogo.png`,
            caption: ''
        },
        {
            image: `${process.env.PUBLIC_URL}\\images\\ProgramaRenascerImage1.png`,
            caption: ''
        }
    ];

    return (
        <>
            <DesktopOrLaptop>
                <Wrapper desktop>
                    <StyledSpanText>
                        <p> Trabalho voltado aos dependentes e seus familiares que promove a
                            autotransformao, visando a insero, consciente e harmoniosa, no
                            ncleo  familiar e na sociedade. </p>
                        <p> No tem interesse na transformao religiosa do indivduo, mas sua
                            conscientizao para seu prprio processo em busca de Deus. So
                            desenvolvidos estudos e debates ao longo do ano, em busca da resoluo
                            das dificuldades com relao a dependncia/codependncia, a baixa
                            autoestima, o complexo de inferioridade, sentimentos de culpa, etc. </p>
                        <p> Este trabalho visa, em especial, o amparo aos dependentes de drogas,
                            lcool e demais dependncias e compulses. </p>
                        <p> Aberta a todos que necessitem de tal amparo,  realizada uma reunio
                            semanal. </p>
                        <p> Lema: <Bold>&quot;Ame a vida, no as drogas&quot;</Bold> <Italic>(Luiz Antnio - esprito)</Italic> </p>
                    </StyledSpanText>
                    <StyledDiv>
                        <StyledSpanTitle>
                            Itens Principais
                        </StyledSpanTitle>
                        <StyledDivContent>
                            <StyledSpanText>
                                <p> Composto de 14 passos/mdulos subdivididos originalmente em 188 reunies
                                    -  h necessidade de participar das reunies (atualmente online). </p>
                                <p> O vcio  a manifestao externa da dependncia espiritual. </p>
                                <p> O objetivo  a libertao da dependncia espiritual. </p>
                                <p> Fundamental a vontade de mudar. </p>
                                <p> Considerar um tratamento mnimo de dois anos. </p>
                                <p> Plano 24 horas: estabelecer compromisso consigo mesmo de evitar o uso do
                                    produto de dependncia e tambm daquele causador dos vcios morais, por 24
                                    horas, assim vencendo sempre a cada dia. </p>
                                <p> Reforma ntima: processo de autoconhecimento para mudar nossa estrutura de
                                    pensamento. </p>
                            </StyledSpanText>
                        </StyledDivContent>
                    </StyledDiv>
                    <StyledDiv>
                        <StyledSpanTitle>
                            Perguntas Frequentes
                        </StyledSpanTitle>
                        <StyledDivContent>
                            <Accordion listOfItems={questionsAndAnswers} />
                        </StyledDivContent>
                    </StyledDiv>
                    <StyledDiv>
                        <Carousel data={images} />
                    </StyledDiv>
                </Wrapper>
            </DesktopOrLaptop>
            <TabletOrMobile>
                <Wrapper>
                    <StyledSpanText>
                        <p> Trabalho voltado aos dependentes e seus familiares que promove a
                            autotransformao, visando a insero, consciente e harmoniosa, no
                            ncleo  familiar e na sociedade. </p>
                        <p> No tem interesse na transformao religiosa do indivduo, mas sua
                            conscientizao para seu prprio processo em busca de Deus. So
                            desenvolvidos estudos e debates ao longo do ano, em busca da resoluo
                            das dificuldades com relao a dependncia/codependncia, a baixa
                            autoestima, o complexo de inferioridade, sentimentos de culpa, etc. </p>
                        <p> Este trabalho visa, em especial, o amparo aos dependentes de drogas,
                            lcool e demais dependncias e compulses. </p>
                        <p> Aberta a todos que necessitem de tal amparo,  realizada uma reunio
                            semanal. </p>
                        <p> Lema: <Bold>&quot;Ame a vida, no as drogas&quot;</Bold> <Italic>(Luiz Antnio - esprito)</Italic> </p>
                    </StyledSpanText>
                    <StyledDiv>
                        <StyledSpanTitle>
                            Itens Principais
                        </StyledSpanTitle>
                        <StyledDivContent>
                            <StyledSpanText>
                                <p> Composto de 14 passos/mdulos subdivididos originalmente em 188 reunies
                                    -  h necessidade de participar das reunies (atualmente online). </p>
                                <p> O vcio  a manifestao externa da dependncia espiritual. </p>
                                <p> O objetivo  a libertao da dependncia espiritual. </p>
                                <p> Fundamental a vontade de mudar. </p>
                                <p> Considerar um tratamento mnimo de dois anos. </p>
                                <p> Plano 24 horas: estabelecer compromisso consigo mesmo de evitar o uso do
                                    produto de dependncia e tambm daquele causador dos vcios morais, por 24
                                    horas, assim vencendo sempre a cada dia. </p>
                                <p> Reforma ntima: processo de autoconhecimento para mudar nossa estrutura de
                                    pensamento. </p>
                            </StyledSpanText>
                        </StyledDivContent>
                    </StyledDiv>
                    <StyledDiv>
                        <StyledSpanTitle>
                            Perguntas Frequentes
                        </StyledSpanTitle>
                        <StyledDivContent>
                            <Accordion listOfItems={questionsAndAnswers} />
                        </StyledDivContent>
                    </StyledDiv>
                    <StyledDiv>
                        <Carousel data={images} />
                    </StyledDiv>
                </Wrapper>
            </TabletOrMobile>
        </>
    );
};

export default ContentSection;