import React from 'react';
import PropTypes from 'prop-types';
import { Pages } from '../../../shared/enums/Pages';
import { Bold } from './index.style';
import TemplateSection from '../TemplateSection';

const TrabalhoReligiosoSection = ({ rightImage }) => {

    const availableServices = Object.values(Pages).map(page => page.name);
    availableServices.shift();

    return (
        <TemplateSection
            rightImage={rightImage}
            section={Pages.TrabalhoReligioso}
            image={`${process.env.PUBLIC_URL}\\images\\Facade.png`}
            text={
                <>
                    <p> A <Bold>Fraternidade Esprita Clara de Fontaine</Bold>, fundada em 02 de setembro de 1994,
                         uma organizao religiosa, com atividades nas reas assistencial, cultural, beneficente e
                        filantrpica, sem fins lucrativos, de prazo indeterminado e tem domiclio, sede e foro na cidade
                        de Belo Horizonte, estado de Minas Gerais, no endereo da Rua Trs Pontas, n 1.137 B, Bairro
                        Carlos Prates.</p>
                    <p> A casa oferece, de forma inteiramente gratuita, os seguintes servios: {availableServices.join('; ')}.</p>
                </>
            }
        />
    );
};

TrabalhoReligiosoSection.propTypes = {
    rightImage: PropTypes.bool
};

TrabalhoReligiosoSection.defaultProps = {
    rightImage: false
};

export default TrabalhoReligiosoSection;