import React from 'react';
import DesktopOrLaptop from '../../../shared/devices/DesktopOrLaptop';
import TabletOrMobile from '../../../shared/devices/TabletOrMobile';
import { Pages } from '../../../shared/enums/Pages';
import Carousel from '../../shared/Carousel';
import { Wrapper, StyledSpanText, StyledSpanTitle, StyledDiv, StyledDivContent, Bold } from './index.style';

const ContentSection = () => {

    const availableServices = Object.values(Pages).map(page => page.name);
    availableServices.shift();

    const images = [
        {
            image: `${process.env.PUBLIC_URL}\\images\\Facade.png`,
            caption: ''
        },
        {
            image: `${process.env.PUBLIC_URL}\\images\\Facade2.png`,
            caption: ''
        },
        {
            image: `${process.env.PUBLIC_URL}\\images\\TrabalhoReligiosoImage.png`,
            caption: ''
        }
    ];

    return (
        <>
            <DesktopOrLaptop>
                <Wrapper desktop>
                    <StyledSpanText>
                        <p> Como Casa Esprita, atua na educao religiosa, ensinando o Evangelho em
                            conjunto com a preparao do indivduo para o enfrentamento das dificuldades
                            da vida, de forma que ele se sinta capaz e motivado a reverter qualquer
                            processo que entenda como negativo, fortalecendo-o contra pensamentos de
                            autodestruio. </p>
                        <p> Tenta dar ao educando as possibilidades de encarar a vida, o mundo e
                            as pessoas de maneira mais espiritualizada e menos materialista.</p>
                        <p> Como lema da Casa Esprita, <Bold>&quot;fora da caridade no h salvao&quot; </Bold>
                            mostramos a necessidade da prtica do amor ao prximo ensinada pelo Cristo,
                            sendo esta a principal meta de nossa Casa.</p>
                    </StyledSpanText>
                    <StyledDiv>
                        <StyledSpanTitle>
                            Servios prestados
                        </StyledSpanTitle>
                        <StyledDivContent>
                            <StyledSpanText>
                                <p> A casa oferece os seguintes servios:
                                    {availableServices.join('; ')}.</p>
                                <p> Todos os servios e demais atividades prestados por esta Instituio
                                    so inteiramente gratuitos, no podendo os membros da administrao e
                                    demais trabalhadores, todos voluntrios no remunerados, receber
                                    qualquer espcie de pagamento, sob qualquer forma.  </p>
                                <p> A Entidade no distribui resultados, dividendos, bonificaes,
                                    participaes ou parcelas de seu patrimnio, sob nenhuma forma
                                    ou pretexto. </p>
                                <p> A Sociedade  mantida exclusivamente por doaes espontneas provindas
                                    de seus administradores e tarefeiros voluntrios e eventuais promoes
                                    para tal fim. </p>
                            </StyledSpanText>
                        </StyledDivContent>
                    </StyledDiv>
                    <StyledDiv>
                        <Carousel data={images} />
                    </StyledDiv>
                </Wrapper>
            </DesktopOrLaptop>
            <TabletOrMobile>
                <Wrapper>
                    <StyledSpanText>
                        <p> Como Casa Esprita, atua na educao religiosa, ensinando o Evangelho em
                            conjunto com a preparao do indivduo para o enfrentamento das dificuldades
                            da vida, de forma que ele se sinta capaz e motivado a reverter qualquer
                            processo que entenda como negativo, fortalecendo-o contra pensamentos de
                            autodestruio. </p>
                        <p> Tenta dar ao educando as possibilidades de encarar a vida, o mundo e
                            as pessoas de maneira mais espiritualizada e menos materialista.</p>
                        <p> Como lema da Casa Esprita, <Bold>&quot;fora da caridade no h salvao&quot; </Bold>
                            mostramos a necessidade da prtica do &quot;amor ao prximo&quot; ensinado pelo Cristo,
                            sendo esta a principal meta de nossa Casa.</p>
                    </StyledSpanText>
                    <StyledDiv>
                        <StyledSpanTitle>
                            Servios prestados
                        </StyledSpanTitle>
                        <StyledDivContent>
                            <StyledSpanText>
                                <p> A casa oferece os seguintes servios:
                                    {availableServices.join('; ')}.</p>
                                <p> Todos os servios e demais atividades prestados por esta Instituio
                                    so inteiramente gratuitos, no podendo os membros da administrao e
                                    demais trabalhadores, todos voluntrios no remunerados, receber
                                    qualquer espcie de pagamento, sob qualquer forma.  </p>
                                <p> A Entidade no distribui resultados, dividendos, bonificaes,
                                    participaes ou parcelas de seu patrimnio, sob nenhuma forma
                                    ou pretexto. </p>
                                <p> A Sociedade  mantida exclusivamente por doaes espontneas provindas
                                    de seus administradores e tarefeiros voluntrios e eventuais promoes
                                    para tal fim. </p>
                            </StyledSpanText>
                        </StyledDivContent>
                    </StyledDiv>
                    <StyledDiv>
                        <Carousel data={images} />
                    </StyledDiv>
                </Wrapper>
            </TabletOrMobile>
        </>
    );
};

export default ContentSection;