import React from 'react';
import { Icon } from 'semantic-ui-react';
import DesktopOrLaptop from '../../../shared/devices/DesktopOrLaptop';
import TabletOrMobile from '../../../shared/devices/TabletOrMobile';
import Carousel from '../../shared/Carousel';
import { Wrapper, StyledDiv, StyledDivContent, StyledSpanTitle, StyledSpanText, Bold } from './index.style';

const ContentSection = () => {

    const images = [
        {
            image: `${process.env.PUBLIC_URL}\\images\\EducacaoMediunicaImage.png`,
            caption: ''
        }
    ];

    return (
        <>
            <DesktopOrLaptop>
                <Wrapper desktop>
                    <StyledSpanText>
                        <p> Podemos dizer que o objetivo da <Bold>Educao Medinica</Bold> na Casa de Clara  o exerccio da Mediunidade com
                            Jesus. Com isso, esclarecemos que  a busca do autodesenvolvimento de valores morais e
                            conhecimentos para o atingimento deste objetivo. Podemos comparar a Educao Medinica  situao de uma pessoa que
                            concorre ao sorteio de um automvel e ganha. Ao buscar o automvel, recebe as chaves e no sabe dirigir. Alm
                            de aprender a dirigir, precisa aprender tambm a ter atitudes moralizadas na conduo do automvel. Ou seja, temos a
                            condio fisiolgica da mediunidade, desenvolvemos esta condio atravs do exerccio da mediunidade, e a educamos
                            atravs do autoconhecimento, autotransformao, conhecimentos  e atividades no bem. </p>
                        <p> Temos por lema o <Bold>&quot;dai de graa o que de graa recebestes&quot;</Bold>. </p>
                    </StyledSpanText>
                    <StyledDiv>
                        <StyledSpanTitle>
                            Dinmica do estudo
                        </StyledSpanTitle>
                        <StyledDivContent>
                            <StyledSpanText>
                                <p> Em um contexto presencial, temos 2 momentos: </p>
                                <p> <Icon name='bullseye' color='brown' />
                                    Estudo e Prtica: O estudo do Evangelho Segundo o Espiritismo/estudo de Livro Mediunidade </p>
                                <p> <Icon name='bullseye' color='brown' />
                                    Prtica medinica assistida </p>
                            </StyledSpanText>
                        </StyledDivContent>
                    </StyledDiv>
                    <StyledDiv>
                        <Carousel data={images} />
                    </StyledDiv>
                </Wrapper>
            </DesktopOrLaptop>
            <TabletOrMobile>
                <Wrapper>
                    <StyledSpanText>
                        <p> Podemos dizer que o objetivo da <Bold>Educao Medinica</Bold> na Casa de Clara  o exerccio da Mediunidade com
                            Jesus. Com isso, esclarecemos que  a busca do autodesenvolvimento de valores morais e
                            conhecimentos para o atingimento deste objetivo. Podemos comparar a Educao Medinica  situao de uma pessoa que
                            concorre ao sorteio de um automvel e ganha. Ao buscar o automvel, recebe as chaves e no sabe dirigir. Alm
                            de aprender a dirigir, precisa aprender tambm a ter atitudes moralizadas na conduo do automvel. Ou seja, temos a
                            condio fisiolgica da mediunidade, desenvolvemos esta condio atravs do exerccio da mediunidade, e a educamos
                            atravs do autoconhecimento, autotransformao, conhecimentos  e atividades no bem. </p>
                        <p> Temos por lema o <Bold>&quot;dai de graa o que de graa recebestes&quot;</Bold>. </p>
                    </StyledSpanText>
                    <StyledDiv>
                        <StyledSpanTitle>
                            Dinmica do estudo
                        </StyledSpanTitle>
                        <StyledDivContent>
                            <StyledSpanText>
                                <p> Em um contexto presencial, temos 2 momentos: </p>
                                <p> <Icon name='bullseye' color='brown' />
                                    Estudo e Prtica: O estudo do Evangelho Segundo o Espiritismo/estudo de Livro Mediunidade </p>
                                <p> <Icon name='bullseye' color='brown' />
                                    Prtica medinica assistida </p>
                            </StyledSpanText>
                        </StyledDivContent>
                    </StyledDiv>
                    <StyledDiv>
                        <Carousel data={images} />
                    </StyledDiv>
                </Wrapper>
            </TabletOrMobile>
        </>
    );
};

export default ContentSection;